import { useState, useEffect, useRef } from "react";
import { PageShell } from "../PageShell";
import { WebR } from "@r-wasm/webr";

// We keep the WebR instance outside the component state
// to prevent it from being re-initialized on every render.
const webR = new WebR({
  baseUrl: process.env.PUBLIC_URL + "/webr/",
});

const Introduction = () => {
  // State for the R code in the textarea
  const [rCode, setRCode] = useState("hist(rnorm(1000),\n  main=\"Histogram of 1000 Random Normal Variables\",\n  xlab=\"Value\",\n  col=\"#75AADB\")");
  // State for the console output
  const [output, setOutput] = useState("");
  // State to manage WebR's loading status
  const [isLoading, setIsLoading] = useState(true);
  // State to manage when R code is executing
  const [isRunning, setIsRunning] = useState(false);
  // State for any errors
  const [error, setError] = useState(null);

  // Use a ref to hold a reference to the canvas element
  const canvasRef = useRef(null);

  // Initialize WebR once when the component mounts
  useEffect(() => {
    const init = async () => {
      try {
        await webR.init();
        setOutput("WebR is ready. Type some R code and click Run!");
      } catch (e) {
        console.error("Error initializing WebR:", e);
        setError("Failed to initialize R. Please refresh the page.");
      } finally {
        setIsLoading(false);
      }
    };
    init();
  }, []); // Empty dependency array ensures this runs only once

// --- R Code Execution ---
  const handleRunR = async () => {
    if (!rCode.trim()) {
      setOutput("Please enter some R code to run.");
      return;
    }
    setIsRunning(true);
    setError(null);
    setOutput("Running...");

    let shelter;
    try {
      shelter = await new webR.Shelter();
      // Use the webr::canvas device for plotting
      const result = await shelter.captureR(`
        webr::canvas(width=640, height=480)
        ${rCode}
        dev.off()
      `);

      // ADD THIS LINE FOR DEBUGGING
      console.log('R Result:', result);

      // Display console output
      const consoleOutput = result.output
        .filter((line) => line.type === "stdout" || line.type === "stderr")
        .map((line) => line.data)
        .join("\n");
      setOutput(consoleOutput || "[no console output]");

      // --- PLOTTING LOGIC ---
      const canvas = canvasRef.current;

      // Display the first plot generated (if any)
      if (result.images && result.images.length > 0) {
        const img = result.images[0];
        // 1. Resize the canvas (this also clears it)
        canvas.width = img.width;
        canvas.height = img.height;
        // 2. Get the fresh context *after* resizing
        const context = canvas.getContext("2d");
        // 3. Draw the image
        context.drawImage(img, 0, 0);
      } else {
        // If no new plot, just clear the canvas of any old plot
        const context = canvas.getContext("2d");
        context.clearRect(0, 0, canvas.width, canvas.height);
      }

    } catch (e) {
      console.error("Error executing R code:", e);
      setError(`Error: ${e.message}`);
      setOutput(""); // Clear output on error
    } finally {
      if (shelter) {
        shelter.purge();
      }
      setIsRunning(false);
    }
  };

  // Determine button text based on component state
  const getButtonText = () => {
    if (isLoading) return "Initializing R...";
    if (isRunning) return "Running...";
    return "Run R";
  };

  return (
    <PageShell>
      <h1 className="text-6xl md:text-7xl font-bold bg-gradient-to-r from-blue-600 via-purple-600 to-blue-800 dark:from-blue-400 dark:via-purple-400 dark:to-blue-500 bg-clip-text text-transparent mb-4 animate-fade-in">
        Introduction to Statistics
      </h1>

      <p>Welcome!</p>

      {/* --- R playground --- */}
      <h2 className="mt-8 font-semibold text-gray-800 dark:text-gray-80">
        Try it yourself 🚀
      </h2>
      <textarea
        value={rCode}
        onChange={(e) => setRCode(e.target.value)}
        rows="6"
        className="w-full p-2 border rounded font-mono text-sm"
        disabled={isLoading || isRunning}
        placeholder="Enter your R code here"
      />
      <button
        onClick={handleRunR}
        className="mt-2 px-4 py-2 bg-blue-600 text-white rounded disabled:bg-gray-400 transition-colors"
        disabled={isLoading || isRunning}
      >
        {getButtonText()}
      </button>

      {/* --- Output & Plot Display --- */}
      <div className="mt-4">
        <h3 className="font-semibold text-gray-700 dark:text-gray-200">Console Output</h3>
        <pre className="mt-1 p-2 bg-gray-100 dark:bg-gray-800 rounded text-sm min-h-[4em] whitespace-pre-wrap">
          {output}
        </pre>
        {error && (
          <pre className="mt-2 p-2 bg-red-100 text-red-700 rounded text-sm whitespace-pre-wrap">
            {error}
          </pre>
        )}
      </div>

      <div className="mt-4">
        <h3 className="font-semibold text-gray-700 dark:text-gray-200">Plot</h3>
        <canvas
          ref={canvasRef}
          width="640"
          height="480"
          className="mt-1 border rounded bg-white"
        ></canvas>
      </div>
    </PageShell>
  );
};

export default Introduction;